/* Always stacked */
table.b-table.b-table-stacked[data-v-a7d2f450] {
    width: 100%;
}
table.b-table.b-table-stacked[data-v-a7d2f450],
table.b-table.b-table-stacked > tbody[data-v-a7d2f450],
table.b-table.b-table-stacked > tbody > tr[data-v-a7d2f450],
table.b-table.b-table-stacked > tbody > tr > td[data-v-a7d2f450],
table.b-table.b-table-stacked > tbody > tr > th[data-v-a7d2f450],
table.b-table.b-table-stacked > caption[data-v-a7d2f450] {
    display: block;
}



















































































































/* Hide stuff we can't deal with, or shouldn't show */
table.b-table.b-table-stacked > thead[data-v-a7d2f450],
table.b-table.b-table-stacked > tfoot[data-v-a7d2f450],
table.b-table.b-table-stacked > tbody > tr.b-table-top-row[data-v-a7d2f450],
table.b-table.b-table-stacked > tbody > tr.b-table-bottom-row[data-v-a7d2f450] {
    display: none;
}



















































































































/* inter-row top border */
table.b-table.b-table-stacked > tbody > tr[data-v-a7d2f450] > :first-child {
    border-top-width: 0.4rem;
}



















































































































/* convert TD/TH contents to "cells". Caveat: child elements become cells! */
table.b-table.b-table-stacked > tbody > tr > [data-label][data-v-a7d2f450] {
    display: grid;
    grid-template-columns: 40% auto;
    grid-gap: 0.25rem 1rem;
}



















































































































/* generate row cell "heading" */
table.b-table.b-table-stacked > tbody > tr > [data-label][data-v-a7d2f450]::before {
    content: attr(data-label);
    display: inline;
    text-align: right;
    overflow-wrap: break-word;
    font-weight: bold;
    font-style: normal;
}
